<template>
  <div id="dev-concept" class="devConcept">
    <div class="title">发展理念</div>
    <div class="imgs round">
      <div class="imgBox" v-for="src in roundImgs">
        <img :src="src" alt="" />
      </div>
    </div>
    <div class="imgs">
      <div class="imgBox" v-for="src in lineImgs">
        <img :src="src" alt="" ref="img" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roundImgs: [
        "/img/圆形关键信息展示组件(2).png",
        "/img/圆形关键信息展示组件.png",
        "/img/圆形关键信息展示组件(1).png",
      ],
      lineImgs: ["/img/组 28.png", "/img/组 27.png"],
    };
  },
  mounted() {
    this.resize();
    window.addEventListener("resize", this.resize);
  },
  methods: {
    resize() {
      if (window.innerWidth <= 375) {
        this.$refs.img[1].src = "/img/组 32.png";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.devConcept {
  width: 100%;
}
.imgs {
  display: flex;
  padding: 0 20px;
  justify-content: space-around;
}
.imgs img {
  width: 100%;
  height: 100%;
}
.imgBox:nth-child(2) {
  margin: 0 20px;
}
.imgs:last-child {
  margin-top: 139px;
}

@media screen and (max-width: 800px) {
  .imgs:last-child {
    flex-direction: column;
    justify-items: center;
    align-items: center;
    .imgBox:first-child {
      margin-right: 120px;
      margin-bottom: 60px;
    }
    img {
      width: auto;
      height: auto;
    }
  }
}

@media screen and (max-width: 375px) {
  .imgs:last-child .imgBox:first-child {
    margin-right: 0;
  }
}
</style>